















































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import Chart from "chart.js";
import CStatistics from '@/components/links/CStatistics.vue';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import CCorrelationChart from '@/components/CCorrelationChart.vue';
import MathUtil from '@/typescript/MathUtil.ts';
import StatisticsCalc from '@/typescript/StatisticsCalc.ts';

interface ChartPoint extends Chart.ChartPoint {
  x: number;
  y: number;
}

@Component({
  components: {
    CSelectorSample,
    CStatistics,
    CButton,
    CCorrelationChart,
  },
  head: {
    title: {
      inner: '',
    },
  },
})
export default class CorrelationChart extends Vue {
  // Data
  private dataLinkTitle: string = '';
  private dataButtonText: string = '';
  private dataDotColor: string = '#e37660';
  private dataTextX: string = '';
  private dataTextY: string = '';
  private dataSelectedSampleExampleIndex: number = 0;
  private dataSampleExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '/',
    },
    {
      name: 'high_CC',
      caption: '',
      content: '/',
    },
    {
      name: 'low_CC',
      caption: '',
      content: '/',
    },
    {
      name: 'test',
      caption: 'II',
      content: '/',
    },
    {
      name: 'normal',
      caption: '',
      content: '/',
    },
    {
      name: 'uniform',
      caption: '',
      content: '/',
    },
  ];
  private dataCovariance: string = '';
  private dataCorrelationCoefficient: string = '';
  private dataDatasets: Chart.ChartDataSets[] = [
    {
      label: 'X Y',
      borderColor: this.dataDotColor,
      backgroundColor: this.dataDotColor,
      data: [],
    },
  ];
  private dataChartData: Chart.ChartData = {
    datasets: this.dataDatasets,
  };
  private dataChartOptions: Chart.ChartOptions = {
    responsive: true,
  };

  private created(): void {
    let i: number = 1;
    Vue.set(this.dataSampleExamples[i++], 'content', this.generateSample('high_CC'));
    Vue.set(this.dataSampleExamples[i++], 'content', this.generateSample('low_CC'));
    Vue.set(this.dataSampleExamples[i++], 'content', this.generateSample('test'));
    Vue.set(this.dataSampleExamples[i++], 'content', this.generateSample('normal'));
    Vue.set(this.dataSampleExamples[i++], 'content', this.generateSample('uniform'));
  }

  // Methods
  private generateSample(name: string): string {
    let sampleXs: number[] = [];
    let sampleYs: number[] = [];
    let sampleTextX: string = '';
    let sampleTextY: string = '';
    switch(name) {
      case 'high_CC':
        sampleXs = StatisticsCalc.generateSamples(100, 0, 1);
        sampleYs = StatisticsCalc.generateCorrelationalSamples(sampleXs, 0.9);
        sampleXs = sampleXs.map((value: number): number => { return Number(MathUtil.toFixed(value, 3)); });
        sampleYs = sampleYs.map((value: number): number => { return Number(MathUtil.toFixed(value, 3)); });
        sampleTextX = sampleXs.join(',');
        sampleTextY = sampleYs.join(',');
        break;
      case 'low_CC':
        sampleXs = StatisticsCalc.generateSamples(300, 0, 1);
        sampleYs = StatisticsCalc.generateCorrelationalSamples(sampleXs, -0.3);
        sampleXs = sampleXs.map((value: number): number => { return Number(MathUtil.toFixed(value, 4)); });
        sampleYs = sampleYs.map((value: number): number => { return Number(MathUtil.toFixed(value, 4)); });
        sampleTextX = '"' + sampleXs.join('", "') + '"';
        sampleTextY = '"' + sampleYs.join('", "') + '"';
        break;
      case 'test':
        sampleXs = StatisticsCalc.generateSamples(500, 0, 1);
        sampleYs = StatisticsCalc.generateCorrelationalSamples(sampleXs, 0.5);
        sampleXs = StatisticsCalc.unstandardizeSamples(sampleXs, 172.11, 5.69);
        sampleYs = StatisticsCalc.unstandardizeSamples(sampleYs, 31.73, 18.29);
        sampleXs = sampleXs.map((value: number): number => { return Number(MathUtil.toFixed(value, 0)); });
        sampleYs = sampleYs.map((value: number): number => { return Number(MathUtil.toFixed(value, 0)); });
        sampleTextX = sampleXs.join(' ');
        sampleTextY = sampleYs.join(' ');
        break;
      case 'normal':
        sampleXs = StatisticsCalc.generateSamples(512, 0, 1);
        sampleYs = StatisticsCalc.generateSamples(512, 50, 10);
        sampleXs = sampleXs.map((value: number): number => { return Number(MathUtil.toFixed(value, 3)); });
        sampleYs = sampleYs.map((value: number): number => { return Number(MathUtil.toFixed(value, 1)); });
        sampleTextX = sampleXs.join(' : ');
        sampleTextY = sampleYs.join(' : ');
        break;
      case 'uniform':
        for (let i: number = 0; i < 256; i++) {
          sampleXs.push(MathUtil.randomFloat(-1, 1, 5));
          sampleYs.push(MathUtil.randomFloat(0, 100, 2));
        }
        sampleTextX = sampleXs.join(', ');
        sampleTextY = sampleYs.join(', ');
        break;
    }
    return sampleTextX + '/' + sampleTextY;
  }
  private listenOnSampleExampleSelect($event: Event): void {
    this.dataSelectedSampleExampleIndex = Number(<any>$event);
    let sampleTexts: string[] = this.dataSampleExamples[this.dataSelectedSampleExampleIndex].content.split('/');
    this.dataTextX = sampleTexts[0];
    this.dataTextY = sampleTexts[1];
  }
  private listenInputX($event: Event): void {
    this.dataTextX = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenInputY($event: Event): void {
    this.dataTextY = (<HTMLInputElement>(<any>$event).target).value;
  }
  private calcCorrelation(): void {
    let sampleXs: string[] | number[] | null = this.dataTextX.match(/(\+|\-)?([1-9][0-9]+|[0-9])(\.[0-9]+)?/g);
    let sampleYs: string[] | number[] | null = this.dataTextY.match(/(\+|\-)?([1-9][0-9]+|[0-9])(\.[0-9]+)?/g);
    if (sampleXs !== null && sampleYs !== null) {
      sampleXs = sampleXs.map((value: string): number => {
        return Number(value);
      });
      sampleYs = sampleYs.map((value: string): number => {
        return Number(value);
      });
      let length: number = StatisticsCalc.calcMinimum([sampleXs.length, sampleYs.length, ]);
      sampleXs = sampleXs.splice(0, length);
      sampleYs = sampleYs.splice(0, length);
      let decimalPartCount: number = 0;
      sampleXs.forEach((value: number): void => {
        let count: number = MathUtil.countDecimlPart(value);
        if (decimalPartCount < count) {
          decimalPartCount = count;
        }
      });
      sampleYs.forEach((value: number): void => {
        let count: number = MathUtil.countDecimlPart(value);
        if (decimalPartCount < count) {
          decimalPartCount = count;
        }
      });
      this.dataCovariance = MathUtil.toFixed(StatisticsCalc.calcCovariance(sampleXs, sampleYs), decimalPartCount + 2);
      this.dataCorrelationCoefficient = MathUtil.toFixed(StatisticsCalc.calcCorrelationCoefficient(sampleXs, sampleYs), 4);
      this.pushChartPoints(sampleXs, sampleYs);
    }
  }
  private pushChartPoints(sampleXs: number[], sampleYs: number[]) {
    if (this.dataDatasets[0].data !== undefined) {
      this.dataDatasets[0].data.length = 0;
      let chartPoints: ChartPoint[] = [];
      for (let i: number = 0; i < sampleXs.length; i++) {
        chartPoints.push({x: sampleXs[i], y: sampleYs[i]});
      }
      // Chartjsthis.dataChartData
      this.dataChartData = {};
      Vue.set(this.dataDatasets[0], 'data', chartPoints);
      Vue.set(this.dataChartData, 'datasets', this.dataDatasets);
    }
  }
}
