
import { Component, Prop, Watch, Mixins, Vue } from 'vue-property-decorator';
import { Scatter, mixins } from 'vue-chartjs';
import Chart from "chart.js";

@Component({
  components: {},
})
export default class CCorrelationChart extends Mixins(Scatter, mixins.reactiveProp) {
  @Prop() public chartData!: Chart.ChartData;
  @Prop() public chartOptions!: Chart.ChartOptions;

  private mounted() {
    this.renderChart(this.chartData, this.chartOptions);
  }
}
